                <el-form-item label="职位">
                  <el-select v-model="searchCriteriaForm.roleID"  placeholder="请选择" :clearable = 'true'>
                  <el-option
                  v-for="item in options"
                  :key="item.RoleID"
                  :label="item.Name"
                  :value="item.RoleID">
                </el-option>
              </el-select>
              </el-form-item> -->
