              </el-form-item>
           </el-form>
        </el-col>
        <el-table :data="users" highlight-current-row v-loading="listLoading" style="width: 100%;" border cell-class-name='table_style' header-row-class-name='header_style'>
            <el-table-column type="selection" width="40">
            </el-table-column>
            <el-table-column prop="StaffID" label='#' width="60">
            </el-table-column>
            <el-table-column prop="Name" label="姓名" width="80">
            </el-table-column>
            <el-table-column prop="Groups.name" label="部门" width="150">
            </el-table-column>
            <el-table-column  prop="Role.name" label="职位" width="120">
            </el-table-column>
            <el-table-column prop="RiderJobType.Name" label="职位类型" width="100">
            </el-table-column>
            <el-table-column prop="EditRiderEleID" label="饿了么ID" width="130">
            </el-table-column>
            <el-table-column prop="SexText" label="性别" width="60">
            </el-table-column>
            <el-table-column prop="Age" label="年龄" width="60">
            </el-table-column>
            <!-- <el-table-column prop="WorkNO" label="工号" width="200">
             <template slot-scope="scope">
              <p size="small" @click="editRiderClick(scope.row)">{{scope.row.WorkNO == null?'-': scope.row.WorkNO}}</p> 
    				</template> 
    			</el-table-column> -->
            <el-table-column prop="RecruitChannel.Name" label="招聘渠道" width="100">
            </el-table-column>
            <el-table-column prop="RiderReferrerStaffID" label="介绍人" width="80">
            </el-table-column>
            <el-table-column prop="RiderReferrerDate" label="介绍日期" width="100">
            </el-table-column>
            <el-table-column prop="RiderReferrerAttachmentURL" label="介绍附件" width="100">
            </el-table-column>
            <el-table-column prop="JobNature.Name" label="用工性质" width="100">
            </el-table-column>
            <el-table-column prop="ProtocolNO" label="协议编号" width="80">
            </el-table-column>
            <el-table-column prop="ProtocolTime.Name" label="合同期次" width="100">
            </el-table-column>
            <el-table-column prop="ProtocolType.Name" label="协议类型" width="100">
            </el-table-column>
            <el-table-column prop="ProtocolSignedDate" label="签署日期" width="100">
            </el-table-column>
            <el-table-column label="协议期限" width="200">
                <template slot-scope="scope">
                    <p size="small">{{scope.row.ProtocolBeginDate == null?'-':scope.row.ProtocolBeginDate + ' - ' + (scope.row.ProtocolEndDate == null ? '' :scope.row.ProtocolEndDate)}}</p>
                </template>
            </el-table-column>
            <el-table-column prop="InsuranceNature.Name" label="保险性质" width="100">
            </el-table-column>
            <el-table-column label="保险期限" width="200">
                <template slot-scope="scope">
                    <p size="small">{{scope.row.InsuranceStartBuyDate == null?'-':scope.row.InsuranceStartBuyDate + ' - ' + scope.row.InsuranceStopBuyDate}}</p>
                </template>
            </el-table-column>
            <el-table-column prop="BankType.Name" label="开户行" width="80">
            </el-table-column>
            <el-table-column prop="BankNO" label="银行卡号" width="200">
            </el-table-column>
            </el-table-column>
            <el-table-column prop="Birthday" label="出生日期" width="120">
