            </el-table-column>
            <el-table-column prop="Residence" label="居住地" width="200">
            </el-table-column>
            <el-table-column prop="EmergencyContact" label="紧急联系人" width="100">
            </el-table-column>
            <el-table-column prop="EmergencyContactRelationship" label="关系" width="60">
            </el-table-column>
            <el-table-column prop="EmergencyContactMobile" label="联系人手机号" min-width="130">
            </el-table-column>
            <el-table-column label="操作" width="120" fixed="right">
                <template slot-scope="scope">
                    <el-button size="small" @click="getLeave(scope.row)">请假</el-button>
                </template>
            </el-table-column>
        </el-table>
  <!--工具条-->
		<el-col :span="24" class="toolbar">
		<el-pagination
      @size-change="handlePaginationSizeChange"
      @current-change="handlePaginationCurrentChange"
      :current-page="pagingInfoForm.pageNumber"
      :page-sizes="[10, 50, 100, 200, 400]"
      :page-size="pagingInfoForm.pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="page.totalItemCount"
      style="float:right;"
      v-if="page.totalItemCount"
      >
    </el-pagination>
		</el-col>
        <!-- 请假界面 -->
 <el-dialog :title="requestTitle" :visible.sync="isRequestLeave" :close-on-click-modal="false" class="entry_style">
    <el-form class="small-space" :model="requestLeaveForm" ref="requestLeaveForm" :rules='rolus' label-position="right" label-width="120px" size="mini" style='width: 500px; margin-left:50px;' v-loading="isLoading">
      <el-form-item label="请假类型" prop='StaffLeaveTypeID'>
         <el-select v-model="requestLeaveForm.StaffLeaveTypeID"  placeholder="请选择" :clearable ='isClearable'>
         <el-option
          v-for="item in requestLeaveList"
          :key="item.StaffLeaveTypeID"
          :label="item.Name"
          :value="item.StaffLeaveTypeID">
        </el-option>
      </el-select>
      </el-form-item>
      <el-form-item label="开始时间" class="beginTime_style" :required='true'>
        <el-form-item label-width="0" class="beginDate_style" prop='BeginDate'>
         <el-date-picker v-model="requestLeaveForm.BeginDate" type="date" placeholder="选择开始请假时间" format="yyyy 年 MM 月 dd 日"
      value-format="yyyy-MM-dd">
        </el-date-picker>
        </el-form-item>
      <el-form-item label-width="10px" class="beginDate_style" prop='BeginDatePartial'>
      <el-radio-group v-model="requestLeaveForm.BeginDatePartial">
        <el-radio label="1">上午</el-radio>
        <el-radio label="2">下午</el-radio>
      </el-radio-group>
      </el-form-item>
      </el-form-item>
      <el-form-item label="结束时间" class="beginTime_style" :required='true'>
        <el-form-item label-width="0" class="beginDate_style" prop='EndDate'>
         <el-date-picker v-model="requestLeaveForm.EndDate" type="date" placeholder="选择结束请假时间" format="yyyy 年 MM 月 dd 日"
      value-format="yyyy-MM-dd">
        </el-date-picker>
      </el-form-item>
      <el-form-item prop='EndDatePartial' class="beginDate_style" label-width="10px">
      <el-radio-group v-model="requestLeaveForm.EndDatePartial">
        <el-radio label="1">上午</el-radio>
        <el-radio label="2">下午</el-radio>
      </el-radio-group>
      </el-form-item>
      </el-form-item>
      <el-form-item label="请假天数">
        <el-input v-model="requestLeaveForm.Days" :disabled="true"></el-input>
      </el-form-item>
      <el-form-item label="申请备注">
        <el-input v-model="requestLeaveForm.RequestRemark"></el-input>
      </el-form-item>
      <el-form-item label="申请附件1" prop="RequestAttachmentURL1">
         <el-col :span="16">
          <el-input v-model.trim="requestLeaveForm.RequestAttachmentURL1" auto-complete="off" placeholder="申请附件地址1" ref="RequestAttachmentURL1"></el-input>
        </el-col>
        <el-col class="line" :span="1">&nbsp;</el-col>
        <el-col :span="7">
          <el-button type="primary" plain @click="handleChangeHeadURLBrowser">浏览服务器</el-button>
        </el-col>
      </el-form-item>
      <el-form-item label="申请附件2" prop="RequestAttachmentURL2">
         <el-col :span="16">
          <el-input v-model.trim="requestLeaveForm.RequestAttachmentURL2" auto-complete="off" placeholder="申请附件地址2" ref="RequestAttachmentURL2"></el-input>
        </el-col>
        <el-col class="line" :span="1">&nbsp;</el-col>
        <el-col :span="7">
          <el-button type="primary" plain @click="handRequestAttachmentURL2">浏览服务器</el-button>
        </el-col>
      </el-form-item>
    </el-form>
    <div  class="submit"> 
      <div class="buttonStyle">
        <el-button @click="requestLeaveCancel" class="userdButton">取 消</el-button>
        <el-button type="primary" @click="requestLeaveCreate('requestLeaveForm')" class="userdButton">确 定</el-button>
     </div>   
    </div> 
  </el-dialog>
  </el-main>
  </section>
</template>
<script>
import api from '@/utils/api'
import _ from 'lodash'
export default {
  data () {
    var beginchange = (rule, value, callback) => {
      if (!value) {
        callback(new Error('开始时间段不能为空'))
      } else {
        if (!this.requestLeaveForm.BeginDate && !this.requestLeaveForm.EndDate) {
          callback()
        } else {
          this.getDays()
          callback()
        }
      }
    }
    var beginDateChange = (rule, value, callback) => {
      if (!value) {
        callback(new Error('开始时间不能为空'))
      } else {
        if (this.requestLeaveForm.EndDate) {
          const beginDates = new Date(value.replace(/-/g, '/').substr(0, 10)).getTime()
          const endDates = new Date(this.requestLeaveForm.EndDate.replace(/-/g, '/').substr(0, 10)).getTime()
          const timeDifference = parseInt((endDates - beginDates) / 1000 / 60 / 60 / 24)
          if (timeDifference < 0) {
            callback(new Error('请选择正确的开始时间'))
          } else {
            this.getDays()
            callback()
          }
        }
      }
    }
    var endDateChange = (rule, value, callback) => {
      if (!value) {
        callback(new Error('结束时间不能为空'))
      } else {
        if (this.requestLeaveForm.BeginDate) {
          const beginDates = new Date(this.requestLeaveForm.BeginDate.replace(/-/g, '/').substr(0, 10)).getTime()
          const endDates = new Date(value.replace(/-/g, '/').substr(0, 10)).getTime()
          const timeDifference = parseInt((endDates - beginDates) / 1000 / 60 / 60 / 24)
          if (timeDifference < 0) {
            callback(new Error('请选择正确的开始时间'))
          } else {
            this.getDays()
            callback()
          }
        }
      }
    }
    return {
      listLoading: false,
      users: [],
      // dataList: [],
      // options: [],
      // tree: [],
      requestLeaveForm: {
        StaffLeaveTypeID: '',
        TargetStaffID: '',
        Days: '',
        HalfDays: '',
        BeginDate: '',
        BeginDatePartial: '1',
        EndDate: '',
        EndDatePartial: '2',
        RequestRemark: '',
        RequestAttachmentURL1: '',
        RequestAttachmentURL2: ''
      },
      rolus: {
        BeginDatePartial: [
          {validator: beginchange, trigger: 'change'}
        ],
        EndDatePartial: [
          {validator: beginchange, trigger: 'change'}
        ],
        BeginDate: [
          {validator: beginDateChange, trigger: 'change'}
        ],
        EndDate: [
          {validator: endDateChange, trigger: 'change'}
        ],
        StaffLeaveTypeID: [
          { required: true, message: '请假类型不能为空' }
        ]
      },
      requestTitle: '',
      isRequestLeave: false,
      isLoading: false,
      isClearable: true,
      requestLeaveList: [],
      pagingInfoForm: {
        pageNumber: 1,
        pageSize: 10,
        isExcludeMetaData: false,
        sortInfo: {
          sort: 'StaffID',
          sortDir: 'ASC'
        }
      },
      searchCriteriaForm: {
        keyword: null,
        groupIDs: [],
        roleIDs: [],
        StaffStatusIDs: [],
        groupIDPath: []
      },
      page: {
        list: null,
        totalItemCount: null,
        totalPageCount: null
      },
      defaultProps: {
        children: 'Children',
        label: 'Name',
        value: 'GroupID'
      }
    }
  },
  mounted: function () {
    // this.getGroupInfoBaseList()
    this.getStaffBaseList()
    this.getStaffLeaveList()
  },
  methods: {
     // 提交附件1
    handleChangeHeadURLBrowser () {
      this.popupCKFinder('RequestAttachmentURL1')
    },
    // 提交附件2
    handRequestAttachmentURL2 () {
      this.popupCKFinder('RequestAttachmentURL2')
    },
    // popupCKFinder
    popupCKFinder (name) {
      const _this = this
    /* eslint-disable no-undef */
      try {
        CKFinder.popup({
          chooseFiles: true,
          width: 800,
          height: 600,
          onInit: function (finder) {
            finder.on('files:choose', function (evt) {
              const file = evt.data.files.first()
              _this.requestLeaveForm[name] = file.getUrl()
            })
            finder.on('file:choose:resizedImage', function (evt) {
              _this.requestLeaveForm[name] = evt.data.resizedUrl
            })
          }
        })
      } catch (e) {
        console.log(e.message)
      }
    },
    // 清空请假列表
    resetRequestLeaveForm () {
      this.requestLeaveForm = {
        StaffLeaveTypeID: '',
        TargetStaffID: '',
        Days: '',
        BeginDate: '',
        BeginDatePartial: '1',
        EndDate: '',
        EndDatePartial: '2',
        RequestRemark: '',
        RequestAttachmentURL1: '',
        RequestAttachmentURL2: ''
      }
      var _this = this
      this.$nextTick(() => {
        _this.$refs['requestLeaveForm'].clearValidate()
      })
    },
    // 部门选择部门
    // handGroupIDChange (val) {
    //   const _index = val.length - 1
    //   const groupID = val[_index]
    //   this.searchCriteriaForm.groupID = groupID
    //   const sourceList = this.sourceList
    //   for (let i = 0; i < sourceList.length; i++) {
    //     if (sourceList[i].GroupID === groupID) {
    //       this.options = sourceList[i].Roles
    //       // console.long('数据s' + sourceList[i])
    //     }
    //   }
    // },
    // 获取部门数据
    // getGroupInfoBaseList () {
    //   api.GetGroupInfoBaseList().then(response => {
    //     this.sourceList = response.data.list
    //     this.generateTree()
    //     this.dataList = this.tree
    //     // console.log('success' + JSON.stringify(this.tree))
    //   }, error => {
    //     console.log('errors' + JSON.stringify(error))
    //   })
    // },
    // 添加到总部下
    // generateTree () {
    //   const pid = this.sourceList[0].GroupID
    //   for (let i = 1; i < this.sourceList.length; i++) {
    //     if (this.sourceList[i].ParentID === pid) {
    //       const item = {
    //         GroupID: this.sourceList[i].GroupID,
    //         Name: this.sourceList[i].Name,
    //         // Children: [],
    //         Roles: this.sourceList[i].Roles,
    //         Level: this.sourceList[i].Level
    //       }
    //       this.tree.push(item)
    //       this.addChildren(item, i)
    //     }
    //   }
    // },
    // 递归排列职位
    // addChildren (item, currentIndex) {
    //   for (let i = currentIndex + 1; i < this.sourceList.length; i++) {
    //     if (this.sourceList[i].ParentID === item.GroupID) {
    //       const child = {
    //         GroupID: this.sourceList[i].GroupID,
    //         Name: this.sourceList[i].Name,
    //         // Children: [],
    //         Roles: this.sourceList[i].Roles,
    //         Level: this.sourceList[i].Level
    //       }
    //       if (!item.Children) {
    //         item.Children = []
    //       }
    //       item.Children.push(child)
    //       this.addChildren(child, i)
    //     }
    //   }
    // },
   // 获取员工
    getLeave (scope) {
      this.resetRequestLeaveForm()
      this.isRequestLeave = true
      this.isLoading = false
      this.requestLeaveForm.TargetStaffID = scope.StaffID
      this.requestTitle = scope.Name
      console.log('员工' + JSON.stringify(scope))
    },

   // 获取请假列表
    getStaffLeaveList () {
      api.getStaffLeaveTypeInfoBaseList().then(response => {
        this.requestLeaveList = response.data.list
      }, error => {
        this.showErrorMessage(error.message)
      })
    },
    // 请假取消
    requestLeaveCancel () {
      this.isRequestLeave = false
      this.isLoading = false
    },
    // 请假确定
    requestLeaveCreate (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.isLoading = true
          const requestLeaveList = this.requestLeaveForm
          console.log('请假数据' + JSON.stringify(requestLeaveList))
          api.getRequestStationLeave(requestLeaveList).then(response => {
            this.showSuccessMessage(response.data.message)
            this.isLoading = false
            this.isRequestLeave = false
          }, error => {
            this.showErrorMessage(error.message)
            this.isLoading = false
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // 确定请假函数
    getDays () {
      const beginDates = new Date(this.requestLeaveForm.BeginDate.replace(/-/g, '/').substr(0, 10)).getTime()
      const endDates = new Date(this.requestLeaveForm.EndDate.replace(/-/g, '/').substr(0, 10)).getTime()
      const timeDifference = parseInt((endDates - beginDates) / 1000 / 60 / 60 / 24)
      const beginPartial = this.requestLeaveForm.BeginDatePartial
      const endPartial = this.requestLeaveForm.EndDatePartial
      if (timeDifference < 0) {
        this.showErrorMessage('请选择正确的时间')
      } else {
        if (beginPartial === '1' && endPartial === '1') {
          this.requestLeaveForm.Days = timeDifference + 0.5
        } else if (beginPartial === '1' && endPartial === '2') {
          this.requestLeaveForm.Days = timeDifference + 1
        } else if (beginPartial === '2' && endPartial === '1') {
          if (timeDifference !== 0) {
            this.requestLeaveForm.Days = timeDifference + 0
          } else {
            this.showErrorMessage('请选择正确的时间')
          }
        } else {
          this.requestLeaveForm.Days = timeDifference + 0.5
        }
        this.requestLeaveForm.HalfDays = parseInt(this.requestLeaveForm.Days * 2)
      }
    },
    // 查询
    getUsersed () {
      this.getStaffBaseList()
    },
    // 获取全部请假数据
    getUsersAll () {
      this.resetsearchCriteriaForm()
      this.getStaffBaseList()
    },
    // 清空搜索条件
    resetsearchCriteriaForm () {
      this.searchCriteriaForm = {
        keyword: '',
        groupID: '',
        roleID: '',
        groupIDPath: []
      }
    },
     // 分页
    handlePaginationSizeChange (val) {
      this.pagingInfoForm.pageSize = val
      this.pagingInfoForm.pageNumber = 1
      this.getStaffBaseList()
    },
    handlePaginationCurrentChange (val) {
      this.pagingInfoForm.pageNumber = val
      this.getStaffBaseList()
    },
    // 分页结束
     // 错误信息
    showErrorMessage (message) {
      this.showErrorMessage(message)
    },
    // 成功信息
    showSuccessMessage (message) {
      this.showSuccessMessage(message)
    },
  // 获取员工列表
    getStaffBaseList () {
      var _this = this
      const params = _.extend({}, this.pagingInfoForm, this.searchCriteriaForm)
      api.GetStaffBaseList(params).then(response => {
        this.page = response.data.page
        const list = response.data.page.list
        this.users = list
        for (let i = 0; i < list.length; i++) {
          if (list[i].Sex === 1) {
            list[i].SexText = '男'
          } else {
            list[i].SexText = '女'
          }
          if (list[i].MaritalStatus === 1) {
            list[i].MaritalStatusText = '未婚'
          } else if (list[i].MaritalStatus === 2) {
            list[i].MaritalStatusText = '已婚'
          } else {
            list[i].MaritalStatus = ''
          }
          if (list[i].StaffStatus.StaffStatusID === 2 || list[i].IsInBlackList) {
            list[i].showStaff = false
            list[i].showInBlack = false
            if (list[i].StaffStatus.StaffStatusID === 2) {
              list[i].showExit = true
            }
          } else {
            list[i].showInBlack = true
            list[i].showExit = false
            list[i].showStaff = true
          }
          if (list[i].Birthday != null) {
            const index = list[i].Birthday.indexOf(' ')
            const str = list[i].Birthday.substr(0, index)
            list[i].Birthday = str
          }
          if (list[i].ProtocolSignedDate != null) {
            const signeIndex = list[i].ProtocolSignedDate.indexOf(' ')
            const strSigne = list[i].ProtocolSignedDate.substr(0, signeIndex)
            list[i].ProtocolSignedDate = strSigne
          }
          if (list[i].ProtocolBeginDate != null) {
            const beginIndex = list[i].ProtocolBeginDate.indexOf(' ')
            const strBegin = list[i].ProtocolBeginDate.substr(0, beginIndex)
            list[i].ProtocolBeginDate = strBegin
          }
          if (list[i].ProtocolEndDate != null) {
            const endIndex = list[i].ProtocolEndDate.indexOf(' ')
            const strEnd = list[i].ProtocolEndDate.substr(0, endIndex)
            list[i].ProtocolEndDate = strEnd
          }
          if (list[i].InsuranceStartBuyDate != null) {
            const startIndex = list[i].InsuranceStartBuyDate.indexOf(' ')
            const strStart = list[i].InsuranceStartBuyDate.substr(0, startIndex)
            list[i].InsuranceStartBuyDate = strStart
          }
          if (list[i].InsuranceStopBuyDate != null) {
            const stopIndex = list[i].InsuranceStopBuyDate.indexOf(' ')
            const strStop = list[i].InsuranceStopBuyDate.substr(0, stopIndex)
            list[i].InsuranceStopBuyDate = strStop
          }
        }
      }, error => {
      // console.log('error' + JSON.stringify(error)
        _this.showErrorMessage(error.message)
      })
    }
  }
}
</script>
<style scodived lang="scss">
.entry_style>div{
    min-width:600px;
    width: 30%;
}
.table_style{
  text-align: center;
}
.el-table th{
   text-align: center;
}
.el-table td{
  padding: 0;
}
.dropdown_children{
  width: 146px;
  height: 100%;
  margin-left: 15px;
}
.userdButton{
  margin-top:10px; 
}
.input_style{
  width: 200px;
}
.optionStyle{
 height:300px;
 padding:0;
 background-color: #ffffff 
}
.p_Style{
  width: 100%;
  height: 100%;
  line-height: 100%;
}
.el-dropdown-menu{
  min-width: 200px;
  max-height: 300px;
  padding: 10px 10px;
  overflow-y: scroll;
}
.el_icon{
  position: absolute;
  width: 30px;
  height:27px;
  top: 1px;
  right:2px;
  text-align: center;
  line-height: 27px;
  color: #b4bccc;
}
.buttonStyle{
  width: 200px;
  height:100%;
  position: absolute;
  top:0;
  left: 50%;
  margin-left: -100px;
}
.submit{
  position: relative;
  width: 100%;
  height: 50px;
}
.el-dialog__body{
 padding: 30px 20px;
}
.beginDate_style{
  float: left;
}
.begin_style{
  float: right;
}
.beginTime_style{
  margin-bottom:0!important;
}
</style>



